import { Component, useEffect, ChangeEvent } from "react";
import { ethers } from "ethers";
import contract from "../contracts/project_number_nft_beta.json";

declare let window:any;

class ContractMetaMaskService {
    async connect() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }
        
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        let userAddress = await signer.getAddress();
        let network = await provider.getNetwork();

        if(network.chainId !== 4) {
            let account = JSON.stringify({ "connected": true, "userAddress": userAddress, "chainId": network.chainId, "chainName": network.name });
        
            return account;
        }

        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let owner = await contractinfo.owner();

        let isOwner = false;
        if(userAddress === owner) {
            isOwner = true;
        }

        let account = JSON.stringify({ "connected": true, "isOwner": isOwner, "userAddress": userAddress, "chainId": network.chainId, "chainName": network.name });
        
        return account;
    }

    async getContract() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let name = await contractinfo.name();
        let symbol = await contractinfo.symbol();
        let maxSupply = await contractinfo.maxSupply();
        let totalSupply = await contractinfo.totalSupply();
        let maxMintAmount = await contractinfo.maxMintAmount();
        let owner = await contractinfo.owner();
        let baseURI = await contractinfo.baseURI();
        let viewCost = await contractinfo.cost();

        let info = JSON.stringify({ "name": name, "symbol": symbol, "maxSupply": maxSupply.toString(), "totalSupply": totalSupply.toString(), "maxMintAmount": maxMintAmount.toString(), "owner": owner, "baseURI": baseURI, "viewCost": ethers.utils.formatEther(viewCost).toString() });

        return info;
    }

    async getAdminFunction() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let saleActive = await contractinfo.saleActive();
        let cost = await contractinfo.cost();
        let contractBalance = await provider.getBalance(contract.address);

        let info = JSON.stringify({ "saleActive": saleActive, "cost": ethers.utils.formatEther(cost).toString(), "contractBalance": ethers.utils.formatEther(contractBalance)});

        return info;
    }

    async getSalesStatus() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let saleActive = await contractinfo.saleActive();

        let info = JSON.stringify({ "saleActive": saleActive });

        return info;
    }

    async setSalesStatus(value: boolean) {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        const tx = await contractinfo.setSaleActive(value);
        let info = JSON.stringify({ "tx": tx });

        return info;
    }

    async setCost(value: any) {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let amount = ethers.utils.parseEther(value);
        const tx = await contractinfo.setCost(amount);
        let info = JSON.stringify({ "tx": tx });

        return info;
    }

    async mint() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        let userAddress = await signer.getAddress();
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let mintCost = await contractinfo.cost();

        let overrides = {
            value: mintCost
            //value: ethers.utils.parseEther("0.001")
        };
        try {
            const tx = await contractinfo.mint(userAddress, 1, overrides);
            let info = JSON.stringify({ "tx": tx });

            return info;
        } catch (e:any) {
            let error = JSON.stringify({ "error": e });
            return error;
        }
        
    }

    async withdraw(value: any) {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let amount = ethers.utils.parseEther(value);
        const tx = await contractinfo.withdraw(amount);
        let info = JSON.stringify({ "tx": tx });

        return info;
    }

    async withdrawAll() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        const tx = await contractinfo.withdrawAll();
        let info = JSON.stringify({ "tx": tx });

        return info;
    }

    async getAccountCollection() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        let userAddress = await signer.getAddress();
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let collection = await contractinfo.walletOfOwner(userAddress);
        let info = JSON.stringify({ "collection": collection });

        return info;
    }

}

export default new ContractMetaMaskService();